	import uniIcons from '@/components/uni-icons/uni-icons.vue'
	export default {
		components: {uniIcons},
		data() {
			return {
				imgUrl: "",
				id: "", //商家的id 拿来请求
				info: {}, //商家店面的信息
			}
		},
		onLoad(option){
			this.imgUrl = this.$store.state.imgUrl;
			console.log(option);
			this.api({
				method: "get",
				url: "Shop/shop_info",
				data: {
					id: option.id
				}
			}).then(res=> {
				console.log(res);
				res.data.date.keyword = res.data.date.keyword.split(",");
				this.info = res.data.date;
				this.info.distance = option.distance;
				this.info.shop_socre = option.shop_socre;
				console.log(this.info);
			})
		},
		methods: {
			toSinglePage(single) {
				console.log(single);
				uni.navigateTo({
					url: "./single_shop?id=" + single.id + "&distance=" + this.info.distance
				})
			}
		}
 	}
